<template>
  <div>
    <div class="btn" @click="goMenu">目录</div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {

    }
  },
  methods:{
    goMenu(){
      this.$router.push({
        path: '/catalog',
      })
    }
  }
}
</script>

<style scoped>
.btn{
  width: 200px;
  height: 50px;
  background: red;
}
</style>



