<template>
  <div>
    <h2>目录</h2>
    <ul>
      <li v-for="item in menus" :key="item.id" @click="goArticle(item)">
        {{ item }}
      </li>
    </ul>
  </div>
</template>

<script>
import { menuList } from '../data/index'
export default {
  name: 'HelloWorld',
  data () {
    return {
      menus: menuList
    }
  },
  mounted(){
  },
  methods: {
    goArticle(title){
      this.$router.push({
        path: '/article',
        query: {
          title
        }
      })
    }
  }
}
</script>

<style scoped>
li{
  text-decoration: underline;
}
</style>



