<template>
  <div>
    <span v-for="(item, index) in contentArr" :key="index">
      <a v-if="item.isLink" @click="goPath(item.text)" style="color: blue;">{{item.text}}</a>
      <span v-else>{{item.text}}</span>
    </span>
    <!-- <div v-html="content"></div> -->
  </div>
</template>

<script>
import { map } from '../data/index'
export default {
  name: 'HelloWorld',
  data () {
    return {
      title: '',
      content: '',
      keyArr: [],
      contentArr: []
    }   
  },
  watch:{
    '$route'(to, from){
      console.log(to, from)
      this.title = to.query.title
      this.initContent(this.title)
    }
  },
  mounted(){
    // console.log(this.$route.query)
    // this.msg = this.msg.replace('唧唧复唧唧', '<a href="/">哈哈哈哈</a>')

    this.title = this.$route.query.title

    this.initContent(this.title)
  },
  methods: {
    goPath(title){
      console.log('ddddd')
      this.$router.push({
        path: '\article',
        query: {
          title
        }
      })
    },
    initContent(title){
      this.content = map.get(title).content
      let keyArr = []
      for (const key of map.keys()) {
        if(this.content.indexOf(key) > -1){
          keyArr.push(key)
          var str = `","${key}","`
          this.content = this.content.replace(eval(`/${key}/g`), str)
        }
      }
      this.contentArr = eval(`["${this.content}"]`)
      console.log('contentArr',this.contentArr)
      this.contentArr = this.contentArr.map(item => {
        let isLink = keyArr.indexOf(item) > -1 && item !== this.title
        return {
          text: item,
          isLink
        }
      })
    },

  }
}
</script>

<style scoped>

</style>



